.table-entrada {
    width: 60%;
    margin: 10px 0px;
    padding: 5px;
}

.table-entrada, .table-entrada tr, .table-entrada td, .table-entrada th {
    border: 1px solid #73879C;
    padding: 5px;
}

.table-entrada tr {
    font-size: 0.9rem;
}

