body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

header {
    background-color: #2A3F54;
    color: whitesmoke;
    height: 5vh;    
    display: -webkit-flex;    
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

.brand {
    -webkit-flex-basis: 80%;
            flex-basis: 80%;
    font-weight: bold;
    font-size: 1.5rem;
    padding: 10px;    
}

.by-tag {
    -webkit-flex-basis: 20%;
            flex-basis: 20%;
    font-style: italic;
    padding: 10px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
}
footer {
    display: -webkit-flex;
    display: flex;
    margin: 10px;
    padding: 10px;
    background-color: white;
    color: #73879C;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;  
    font-size: 0.75rem;
}
nav {
    display: -webkit-flex;
    display: flex;
    margin: 10px;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    border: 2px solid white;    
}

.box {
    color: #73879C;
    border: 1px solid #73879C;
    background-color: white;
    margin: 10px;
    padding: 10px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-flex-basis: 20%;
            flex-basis: 20%;
    border-radius: 5px;
}

.box .phaseName {
    font-weight: bold;
    margin: 0px 10px;
}

.box p {
    margin: 10px;
    font-size: 0.8rem;    
}

.fa-angle-double-right {
    color: #73879C;
}

.inactive {
    opacity: 0.5;
    -moz-opacity:0.5;
    -webkit-opacity:0.5;
}
.table-entrada {
    width: 60%;
    margin: 10px 0px;
    padding: 5px;
}

.table-entrada, .table-entrada tr, .table-entrada td, .table-entrada th {
    border: 1px solid #73879C;
    padding: 5px;
}

.table-entrada tr {
    font-size: 0.9rem;
}

