    border: 1px solid #73879C;
    border-radius: 5px;
    font-size: 0.8rem;
    padding: 2px;
    margin: 0px 8px;
} */

.entrada-body {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    margin: 10px;
    background-color: white;
    color: #73879C;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-around;
            justify-content: space-around;
}

.dropzone-div {
    -webkit-flex: 1 1;
            flex: 1 1;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    background-color: aliceblue;
    margin: 10px;
    padding: 15px;
    border: 1px solid #73879C;
    border-radius: 5px;
}

.dropzone-div p {
    font-size: 0.9rem;
    text-align: center;
    font-style: italic;
}

.dropzone-div:hover {
    border: 3px solid #73879C;
}

.dropzone-div .arquivos {
    font-size: 0.8rem;
}

.dropzone-div .arquivos span {
    color: darkcyan;
}

button {
    background-color: white;
    color: #73879C;
    font-weight: bold;
    font-size: 1rem;
    width: 200px;
    margin: 25px 10px;
    padding: 15px;
}

button:hover {
    color: white;
    background-color: #73879C;
}

button i {
    margin: 0px 5px;
}

.result-input {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    margin: 10px;
    background-color: white;
    color: #73879C;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-around;
            justify-content: space-around;    
}

.buttons-group {
    display: -webkit-flex;
    display: flex;
}

.msgs {
    border: 1px groove;
    border-radius: 5px;
    width: 80%;
    min-width: 80%;
    margin: 5px;
    padding: 5px;
}

.msgs-header {    
    font-size: 1.1rem;
    text-align: center;
    font-weight: bold;    
}

.msgs-body {
    font-size: 0.9rem;
}

.msg-filename {
    margin: 0px 5px;
    padding: 0px 20px;
    font-weight: bold;
}

.msgs-info {
    color: #6495ED;    
    border-color: #6495ED;
}

.msgs-error {
    color: #FF4500;   
    border-color: #FF4500; 
}

.msgs-alert {
    color: #DAA520;    
    border-color: #DAA520;
}


.App {
  background-color: #F5F5F5;
  min-height: 100vh;
}

.spinner {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  margin: 10px;
  background-color: white;
  color: #73879C;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}

